.App {
  text-align: center;
  background-color: #e9f5eb;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.input-prompt {
  text-align: center;
  font-weight: string;
  font-size: 1.4em;
  padding-bottom: 1em;
}

.navigation {
  padding-top: 1em;
  padding-bottom: 1em;
  color: #6E661B;
}

.button-group-container {
 background-color:#88c9A1;
 padding-left: 5%;
 align-items: center;
}

btn.btn-submit {
  padding-top: 10px
}

.button-row button {
  background-color: white;
  border: 1px solid green;
  padding: 10px 24px;
  cursor: pointer;
  float: left;
  color: #6E661B;
  border-radius: 8px;
  font-size: 16px;
  width: 90%;
}

.button-row button:not(:last-child) {
  border-right: none;
}

/* Clear floats (clearfix hack) */
.button-row:after {
  content: "";
  clear: both;
  display: table;
}

.button-row button:hover {
  background-color: #D8F1E1;
}

.currency-number-input {
  padding-left: 10%;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0;
}
input[type=number], textarea {
  outline:0px !important;
  -webkit-appearance:none;
  box-shadow:none !important;
  border-bottom: 2px solid #6E661B;
  border-top:0px solid !important;
  border-left:0px solid !important;
  border-right:0px solid !important;
  text-align: center;
  width: 80%;
  height: 50%;
  font-size: xx-large;
}
.form-control:focus{
  border-color: #6E661B;
  -webkit-box-shadow: none;
  box-shadow: none;
  text-align: center;
}

.output-results {
  text-align: center;
}
.output-result {
  text-align: center;
  display: inline-block;
  text-align: left;
  font-size: large;
  color:  #6E661B;
}

table {
  border-collapse: separate;
  border-spacing: 20px 0;
}

td {
  padding: 1% 0;
}

.left-text{
  float:left;
}
.right-text{
  float:right;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* This prevents double-tap zooming on iOS */
touch-action: manipulation;
